import React from 'react';
import * as FieldDef from '../../../shared/FieldDefinition/SystemsFeatures';
import * as InputHelper from '../../Shared/InputHelper';
import * as InputValuesHelper from '../../../shared/InputValuesHelper';
import * as Consts from '../../../shared/Consts';
import FormSectionTitle from '../../Shared/FormSectionTitle';

export default class GeneralStructureFeatures extends React.Component {
  render () {
    const {
      InputValues
    } = this.props;

    const FieldProps = {
      ...this.props,
      FieldDefs: FieldDef.SystemsFeaturesFields
    };

    return (
      <>
        <FormSectionTitle Title='General Structure Features' />
        <div className='form_inputs'>
          <div>
            <div className='inputer_form'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Features_SFG_062_Foundation_Type' })}</div>
          </div>
          {
            InputValuesHelper.HasInputValue(InputValues, 'Features_SFG_062_Foundation_Type', 'Other') &&
              <div>
                <div className='inputer_form'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Features_SFG_454_x1_explanation' })}</div>
              </div>
          }
        </div>
        <div className='form_inputs'>
          <div className='inputer_form'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Features_SFG_070_Roof_Type' })}</div>
        </div>

        <div className='form_inputs'>
          <div className='inputer_form'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Features_SFG_461_Roof_Age' })}</div>
        </div>

        <div className='form_inputs'>
          <div className='inputer_form'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Features_SFG_605_Defects' })}</div>
          <div className='inputer_form greyer'>{InputHelper.GetFieldTitle({ ...FieldProps, FieldName: 'Features_SFG_605_n' })}</div>
        </div>

        <div />

        {
          InputValuesHelper.HasInputValue(InputValues, 'Features_SFG_605_Defects', Consts.OPTION_YES_VALUE) &&
            <div>
              <div className='inputer_form'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Features_SFG_605_99_Defects' })}</div>
            </div>
        }

        {
          InputValuesHelper.HasInputValue(InputValues, 'Features_SFG_605_99_Defects', 'Other') &&
            <div>
              <div className='inputer_form'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Features_SFG_605_t_Explanation' })}</div>
            </div>
        }
      </>);
  }
}
