import * as Consts from './Consts';
import { isPossiblePhoneNumber } from 'react-phone-number-input';

export const Validate = (inputValues, fieldDefinitions) => {
  if (inputValues === undefined || fieldDefinitions === undefined) {
    throw new Error('Incorrect Parameters.');
  }

  const validationResults = [];
  fieldDefinitions.forEach(field => {
    const fieldName = field.FieldName;
    const required = (field.Required === true);
    const fieldType = field.Type;
    const maxAllowedValue = field.MaxValue;

    var validationRules = [];
    if (typeof field.DependsOn === 'string') {
      validationRules.push({ field: field.DependsOn, value: field.DependsOnValue });
    } else if (typeof field.DependsOn === 'object' && Array.isArray(field.DependsOn)) {
      validationRules = field.DependsOn;
    }

    // var notMatching = false;
    for (var i = 0; i < validationRules.length; i++) {
      const validationRule = validationRules[i];
      const dependsOnFieldName = validationRule.field;
      if (dependsOnFieldName !== undefined) {
        const dependsOn = fieldDefinitions.find(item => item.FieldName === dependsOnFieldName);
        if (dependsOn !== undefined) {
          if (dependsOn.Type === Consts.FIELD_TYPE_YES_NO_OPTIONS) {
            const dependsOnValue = (validationRule.value !== undefined ? validationRule.value : Consts.OPTION_YES_VALUE);
            if (inputValues[dependsOnFieldName] === undefined ||
              (inputValues[dependsOnFieldName] !== undefined && inputValues[dependsOnFieldName] !== dependsOnValue)) {
              return;
            }
          } else if (dependsOn.Type === Consts.FIELD_TYPE_MULTIPLE_CHIOSES) {
            const dependsOnValue = validationRule.value;
            const dependsOnInputValue = inputValues[dependsOnFieldName];
            if (dependsOnInputValue !== undefined) {
              if (dependsOnInputValue.find(item => item.value === dependsOnValue) === undefined) {
                return;
              }
            } else {
              return;
            }
          } else if (dependsOn.Type === Consts.FIELD_TYPE_CUSTOME_SELECT) {
            const dependsOnValue = validationRule.value;
            const dependsOnInputValue = inputValues[dependsOnFieldName];
            if (dependsOnInputValue === undefined ||
              (dependsOnInputValue !== undefined && dependsOnInputValue !== dependsOnValue)) {
              return;
            }
          } else if (dependsOn.Type === Consts.FIELD_TYPE_OPTIONS) {
            const dependsOnValue = validationRule.value;
            const dependsOnInputValue = inputValues[dependsOnFieldName];
            if (dependsOnInputValue === undefined ||
              (dependsOnInputValue !== undefined && dependsOnInputValue !== dependsOnValue)) {
              return;
            }
          }
        }
      }
    }

    if (required) {
      if (inputValues[fieldName] === undefined) {
        validationResults.push(
          {
            FieldName: fieldName,
            ValidationError: Consts.VALIDATION_ERROR_REQUIRED_FIELD
          });
        return;
      }

      if (field.Type === Consts.FIELD_TYPE_SINGLE_FILE ||
        field.Type === Consts.FIELD_TYPE_MULTIPLE_FILES ||
        field.Type === Consts.FIELD_TYPE_MULTIPLE_CHIOSES) {
        if (inputValues[fieldName].length === 0) {
          validationResults.push(
            {
              FieldName: fieldName,
              ValidationError: Consts.VALIDATION_ERROR_REQUIRED_FIELD
            });
          return;
        }
      } else if (field.Type === Consts.FIELD_TYPE_SINGLE_OPTION) {
        if (inputValues[fieldName] !== true) {
          validationResults.push(
            {
              FieldName: fieldName,
              ValidationError: Consts.VALIDATION_ERROR_REQUIRED_FIELD
            });
          return;
        }
      } else if (field.Type === Consts.FIELD_TYPE_INTEGER || field.Type === Consts.DATA_TYPE_NUMBER) {
        if (inputValues[fieldName] === undefined) {
          validationResults.push(
            {
              FieldName: fieldName,
              ValidationError: Consts.VALIDATION_ERROR_REQUIRED_FIELD
            });
          return;
        }
      } else {
        if (`${inputValues[fieldName]}`.trim() === '') {
          validationResults.push(
            {
              FieldName: fieldName,
              ValidationError: Consts.VALIDATION_ERROR_REQUIRED_FIELD
            });
          return;
        }
      }
    }

    const fieldValue = inputValues[fieldName];
    if (fieldType === Consts.FIELD_TYPE_EMAIL) {
      if (Consts.EMAIL_REX.test(fieldValue) === false) {
        validationResults.push(
          {
            FieldName: fieldName,
            ValidationError: Consts.VALIDATION_ERROR_INVALID_EMAIL
          });
      }
    } else if (fieldType === Consts.FIELD_TYPE_CURRENCY) {
      if (Consts.CURRENCY_REX.test(fieldValue) === false) {
        validationResults.push(
          {
            FieldName: fieldName,
            ValidationError: Consts.VALIDATION_ERROR_CURRENCY
          });
      } else if (maxAllowedValue !== undefined && fieldValue > maxAllowedValue) {
        validationResults.push(
          {
            FieldName: fieldName,
            ValidationError: Consts.VALIDATION_ERROR_NUMBER_TOO_LARGE
          });
      }
    } else if (fieldType === Consts.FIELD_TYPE_INTEGER) {
      if (Consts.INTEGER_REX.test(fieldValue) === false) {
        validationResults.push(
          {
            FieldName: fieldName,
            ValidationError: Consts.VALIDATION_ERROR_INTEGER
          });
      } else if (maxAllowedValue !== undefined && fieldValue > maxAllowedValue) {
        validationResults.push(
          {
            FieldName: fieldName,
            ValidationError: Consts.VALIDATION_ERROR_NUMBER_TOO_LARGE
          });
      }
    } else if (fieldType === Consts.FIELD_TYPE_INTL_TEL) {
      if (isPossiblePhoneNumber(fieldValue) === false) {
        validationResults.push(
          {
            FieldName: fieldName,
            ValidationError: Consts.VALIDATION_ERROR_INTL_PHONE
          });
      }
    }
  });

  console.log(validationResults);
  return validationResults;
};
